import SortTab from '@/components/common/sort-tab/sort-tab.vue';
import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';
export default {
	data() {
		return {
			longitude: '',
			latitude: '',
			list: [],
			sortTabList: [{
					name: '价格',
					val: 'price',
					subTab: [{
						name: '不限',
						val: ''
					}, {
						name: '100以内',
						val: '1'
					}, {
						name: '100-200',
						val: '2'
					}, {
						name: '200-300',
						val: '3'
					}, {
						name: '300以上',
						val: '4'
					}]
				},
				{
					name: '特色',
					val: 'features',
					subTab: [{
						name: '不限',
						val: ''
					}, {
						name: '当地口味',
						val: '1'
					}, {
						name: '主题餐厅',
						val: '2'
					}, {
						name: '下午茶',
						val: '3'
					}, {
						name: '老字号',
						val: '4'
					}, {
						name: '深夜营业',
						val: '5'
					}, {
						name: '景观餐厅',
						val: '6'
					}],
				},
				{
					name: '综合',
					val: 'synthesis',
					subTab: [{
						name: '不限',
						val: ''
					}, {
						name: '距离最近',
						val: '1'
					}, {
						name: '人均最低',
						val: '2'
					}, {
						name: '人均最高',
						val: '3'
					}],
				}
			],
			condition: {
				price: '',
				features: '',
				synthesis: '',
				longitude: '',
				latitude: ''
			},
			label: []
		}
	},
	components: {
		SortTab
	},
	onLoad() {
		this.getaddress()
	},
	methods: {
		// 获取经纬度
		getaddress() {
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					this.condition.longitude = res.longitude
					this.condition.latitude = res.latitude
					this.getListData();
				},
				fail: err => {
					console.log('定位失败')
					this.condition.longitude = '';
					this.condition.latitude = '';
					
					this.getListData();
				}
			});
		},
		chooseItem(search) {
			let [key, val] = search;
			for (let k in this.condition) {
				if(key === k) { 
					(this.condition[k] = val)
					if(val == '1' && key == 'synthesis') { //距离最近 获取经纬度
						this.getaddress()
					}
					this.getListData()
					return
				}
			}
			// 根据condition去调接口
			this.getListData()
		},
		getListData() {
			uniRequest.post('/food/gethomefoodlist', this.condition).then(res => {
				if(res.data.code == '000000') {
					this.list = res.data.data || []
				} else {
					uni.showToast({
						title: res.data.message
					})
				}
			}).catch(error => {
				console.log(error);
			});
		},
		toDetail(id) {
			uni.navigateTo({
				url: '/pages/specialFood/specialFoodDetail?id=' + id
			})
		}
	}
}
